*,
:after,
:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #102a42;
  background: #f1f5f8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cv-app {
  padding: 50px 30px;
}

.cv-app-title {
  font-size: 40px;
  text-align: center;
  margin: 0 0 30px;
}

.cv-app-content {
  width: 100%;
  max-width: 600px;
  border: 2px solid #ccc;
  background-color: #fff;
  padding: 30px;
  margin: 0 auto;
}

.cv-section + .cv-section {
  margin-top: 30px;
}

.cv-section-title {
  font-size: 26px;
  text-align: center;
  margin: 0 0 20px;
}

.cv-section-form {
  display: flex;
  flex-wrap: wrap;
  gap: 15px 20px;
  margin-top: 20px;
}

.cv-section-form .form-group {
  flex: 1 1 calc(50% - 20px);
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  height: 25px;
  border: 1px solid #cccccc;
  padding: 0 5px;
}

.form-group textarea {
  width: 100%;
  height: 50px;
  border: 1px solid #cccccc;
  padding: 10px;
  resize: none;
}

.submit-btn {
  width: 100px;
  display: flex;
  justify-content: center;
  background: #f1f5f8;
  padding: 5px;
  border: 1px solid #ccc;
  margin: 10px auto 0;
  cursor: pointer;
}

.submit-btn.print {
  margin-top: 30px;
  color: #fff;
  font-weight: 700;
  background: #193e61;
}

.submit-btn.print:hover {
  background: #102a42;
}

.submit-btn:hover {
  background: #e0e4e7;
}

.submit-btn:active {
  box-shadow: inset 0 0 10px #bebebe;
}

.submit-btn:disabled {
  color: #666;
  border-color: #fff !important;
  background: #ccc !important;
  cursor: not-allowed;
}

.form-edit {
  display: flex;
  flex: 1 1 100%;
}

.form-edit + .form-edit {
  margin-top: 20px;
}

.form-edit-content {
  display: flex;
  flex: 1 1 100%;
  flex-direction: column;
  gap: 5px;
}

.form-edit-group {
  display: flex;
  flex-basis: 100%;
}

.form-edit-group strong {
  padding-right: 5px;
}

.form-edit-edit {
  flex: 0 0 24px;
}

.form-edit-edit button {
  background: none;
  border: none;
  cursor: pointer;
}

.form-edit-edit img {
  max-width: 100%;
}

.form-group.btn-group {
  display: flex;
  flex: 1 1 100%;
  justify-content: center;
}

.form-group.btn-group button {
  margin-left: 5px;
  margin-right: 5px;
}

/* MEDIA */

@media print {
  button,
  .cv-app-title,
  .form-edit-edit {
    display: none !important;
  }
  .cv-app-content {
    border: none;
    padding: 0;
  }
  .cv-section-title {
    text-align: left;
    text-decoration: underline;
    margin-bottom: 10px;
  }
